import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);

// 
import {
	cbs,
	gbs
} from 'config/settings.js';

// 
Vue.axios.defaults.baseURL = gbs.host;

/**
 * axios
 * @param  {string}   type      getpost
 * @param  {string}   url       URL
 * @param  {object}   data      
 * @param  {Function} fn        
 * @param  {boolean}   tokenFlag tokentruefalse
 */
module.exports = function(type, url, data, fn, tokenFlag, errFn) {

	// 
	this.$store.dispatch('show_loading');

	if (tokenFlag !== true) {
		data.token = this.$store.state.user.userinfo.token;
	}

	if (type === 'get') {
		var datas = {
			params: data
		};
	} else {
		var datas = data;
	}

	Vue.axios[type](url, datas).then((res) => {
		if (res.data.status === 200) {
			// console.dir(res.data);
			fn(res.data.data);
		} else {

			// 
			cbs.statusError.call(this, res.data);

			if (tokenFlag === true) {
				errFn && errFn.call(this);
			}
		}
		this.$store.dispatch('hide_loading');
	}).catch((err) => {

		//
		// console.log(err);

		this.$store.dispatch('hide_loading');
		cbs.requestError.call(this, err);

		errFn && errFn.call(this);
	});

};


